import { useState } from 'react';
import Collapse from 'react-bootstrap/Collapse';
import Nav from 'react-bootstrap/Nav';

const Presentacion = () => {
    const [open, setOpen] = useState(false);
    
    return (
        <>
            <Nav.Link
                className="link"
                onClick={() => setOpen(!open)}
                aria-controls="example-collapse-text"
                aria-expanded={open}
                variant='outline-secondary'
            >
                Presentacion
            </Nav.Link>
            <Collapse in={open}>
                <div onClick={() => setOpen(!open)} id="example-collapse-text">
                    <div id="presentacion" className='texto-presentacion '>
                        <h1>Bienvenidos a esta landing page en homenaje a la serie de televisin <strong>"The Big Bang Theory"</strong> (TBBT)!</h1>
                        <p>En esta pgina, combinamos la hilarante comedia de <strong>TBBT</strong> y los fascinantes recursos de la Agencia Espacial Estadounidense, la <strong>NASA</strong>. Preprate para explorar el universo de <strong>TBBT</strong> mientras descubres los secretos del cosmos en un solo lugar.</p>
                        <p>Utilizando la potente API de la <strong>NASA</strong>, integramos informacin y datos astronmicos reales en nuestra pgina. Podrs disfrutar de imgenes impresionantes del espacio capturadas por los telescopios ms avanzados, conocer la <strong>"Imagen Astronmica del Da"</strong> seleccionada por la <strong>NASA</strong> y explorar datos importantes de nuestro sistema solar.</p>
                        <p>Adems, hemos creado un buscador de impresionantes imagenes de Marte, captadas por las sondas Rover: <strong>Curiosity, Opportunity y Spirit</strong> de la <strong>NASA</strong>. Podrs realizar bsquedas en Google directamente desde nuestra pagina, para poder acceder a contenido externo, mientras te sumerges en el vasto universo de la ciencia y la astronoma.</p>
                        <p>Ya seas un fantico de Sheldon Cooper, Leonard Hofstadter, Penny o cualquier otro personaje, esta landing page te ofrece una experiencia nica. Podrs maravillarte con imgenes csmicas, aprender datos interesantes y, por supuesto, entretenerte con momentos divertidos de la serie.</p>
                        <p>nete a esta aventura intergalctica y descubre la conexin entre <strong>TBBT</strong> y los secretos del universo! Explora nuestro sitio, sumrgete en el conocimiento y disfruta de una experiencia que combina ciencia, comedia y diversin.</p>
                        <p>Preprate para expandir tus lmites y descubrir las maravillas de <strong>TBBT</strong> y la <strong>NASA</strong> juntas en nuestra landing page!</p>
                    </div>
                </div>
            </Collapse>
        </>
    );
}
 
export default Presentacion;